:root {
  --background: hsl(212, 45%, 89%);
  --card: hsl(0, 0%, 100%);
  --lightText: hsl(212, 45%, 89%);
  --darkText: hsl(218, 44%, 22%);
}

:root {
  --font-outfit: "Outfit", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-outfit);
  font-size: 0.9375rem;
  background: var(--background);
}

.cards {
  text-align: center;
  background: var(--card);
  padding: 1.5625rem;
  border-radius: 0.3125rem;
}

.header {
  font-weight: 700;
  color: var(--darkText);
}

.subText {
  font-family: 400;
  color: var(--lightText);
}